import React from 'react';
import { arrayOf, string, shape, object } from 'prop-types';

import './TrustBlock.scss';

const TrustBlock = ({ params }) => (
  <div className='trustBlock__wrapper'>
    <div className='trustBlock__container'>
      <h2 className='small'>{params.title}</h2>

      <div className='trustBlock__items'>
        {params.items.map((item, index) => (
          <div
            key={index}
            style={{
              backgroundImage: `url(${process.env.ASSETS_URL}${item.image})`
            }}
          />
        ))}
      </div>
    </div>
  </div>
);

TrustBlock.propTypes = {
  params: shape({
    title: string,
    items: arrayOf(object)
  })
};

TrustBlock.defaultProps = {
  params: {
    title: 'Trusted by',
    items: [
      {
        image: 'logos/datastax.jpg'
      },
      {
        image: 'logos/gradle.png'
      },
      {
        image: 'logos/instart.png'
      },
      {
        image: 'logos/newyorklife.jpg'
      }
    ]
  }
};

export default TrustBlock;



